.modal {
min-height: 539px;
width: 720px;
left: 0px;
top: 0px;
border-radius: 40px;
background-color: rgba(28, 28, 33, 1);
transition: opacity 0.3s ease;
}

.modalTop { 
    display: flex;
    justify-content:flex-end;
    margin: 40px 40px 0 40px;
    align-items: center;
}

.title {
    margin-right: auto;
}
