.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.modalHidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.modalVisible {
    opacity: 1;
    visibility: visible;
}